@font-face {
  font-family: "Fivo Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/fivosanslight.woff2") format("woff2"), url("../fonts/fivosanslight.woff") format("woff");
}
@font-face {
  font-family: "Fivo Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/fivosansregular.woff2") format("woff2"), url("../fonts/fivosansregular.woff") format("woff");
}
@font-face {
  font-family: "Fivo Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/fivosansbold.woff2") format("woff2"), url("../fonts/fivosansbold.woff") format("woff");
}
@font-face {
  font-family: "Fivo Sans";
  font-style: normal;
  font-weight: 800;
  font-display: swap;
  src: url("../fonts/fivosansheavy.woff2") format("woff2"), url("../fonts/fivosansheavy.woff") format("woff");
}
@font-face {
  font-family: "Fivo Sans";
  font-style: italic;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/fivosansoblique.woff") format("woff2"), url("../fonts/fivosansoblique.woff") format("woff");
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  font-family: "Fivo sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 40px;
  color: #000000;
  background-color: #ffffff;
}
@media (max-width: 1024px) {
  body {
    font-size: 16px;
    line-height: 30px;
  }
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.sprite {
  display: none;
}
.main {
  width: 100%;
}
.button {
  width: 240px;
  padding-top: 20px;
  padding-bottom: 20px;
  font-size: 20px;
  line-height: 40px;
  text-align: center;
  color: white;
  background-color: #ff6a6d;
  border: none;
  border-radius: 60px 60px;
  box-shadow: -10px 15px 25px -15px red;
  text-decoration: none;
  color: #000000;
  cursor: pointer;
  transition: 0.5s;
}
.button:hover,
.button:focus {
  box-shadow: 0 0 15px 2px #ff6a6d;
}
.button:active {
  box-shadow: none;
}
.header {
  width: 100%;
  background-color: #cef9f9;
  background-image: url("../img/main-beryoga.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
}
.header__wrapper {
  display: flex;
  flex-direction: column;
  width: 1475px;
  margin: 0 auto;
  padding: 60px 50px 454px;
}
.header__nav-container {
  display: flex;
  justify-content: space-between;
  margin-bottom: 160px;
}
.header__logo {
  width: 135px;
  font-weight: 800;
  font-size: 32px;
  line-height: 48px;
  color: #ff6a6d;
}
.header__welcome {
  display: flex;
  flex-direction: column;
  padding-left: 70px;
}
.header__title {
  padding: 0;
  margin: 0;
  font-size: 50px;
  line-height: 60px;
  font-weight: 700;
  width: 620px;
  margin-bottom: 50px;
  white-space: pre-line;
}
.header__description {
  width: 620px;
  padding-right: 145px;
  margin-bottom: 70px;
  font-weight: 300;
  opacity: 0.65;
}
.header__action {
  display: flex;
  justify-content: space-between;
  width: 500px;
}
.header__button {
  color: white;
}
.header__button--sign {
  color: #ff6a6d;
  background-color: rgba(0, 0, 0, 0);
  border: 4px solid #ff6a6d;
}
@media (max-width: 1475px) {
  .header__wrapper {
    width: 1024px;
    padding-bottom: 200px;
  }
  .header__welcome {
    display: flex;
    flex-direction: column;
    padding-left: 0;
  }
}
@media (max-width: 1024px) {
  .header {
    background-position: -350px 0;
  }
  .header__wrapper {
    width: 768px;
  }
  .header__nav-container {
    margin-bottom: 100px;
  }
  .header__title {
    font-size: 35px;
    line-height: 40px;
    font-weight: 700;
  }
  .header__description {
    width: 500px;
    margin-bottom: 120px;
    white-space: normal;
  }
  .header__logo {
    align-self: center;
  }
}
@media (max-width: 768px) {
  .header {
    background-image: none;
  }
  .header__wrapper {
    width: 100%;
    padding-top: 15px;
    padding-bottom: 50px;
  }
  .header__nav-container {
    flex-direction: column;
    margin-bottom: 40px;
  }
  .header__title {
    width: 100%;
    font-size: 28px;
    line-height: 36px;
    margin-bottom: 10px;
  }
  .header__description {
    width: 100%;
    margin-bottom: 30px;
    padding-right: 0;
  }
  .header__action {
    flex-direction: column;
    align-items: center;
    width: auto;
  }
  .header__button {
    margin-bottom: 20px;
  }
}
.navigation {
  display: flex;
  justify-content: flex-end;
  flex-grow: 1;
  margin: 0;
}
.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.navigation__item {
  margin-right: 65px;
  margin-left: 65px;
}
.navigation__link {
  text-decoration: none;
  color: #000000;
  line-height: 36px;
  color: #1c2056;
  text-transform: capitalize;
  opacity: 0.5;
  transition: 0.3s;
}
.navigation__link--active {
  font-weight: 700;
  color: #ff6a6d;
  opacity: 1;
}
.navigation__link:hover,
.navigation__link:focus {
  color: #ff6a6d;
  opacity: 1;
}
.navigation__link:active {
  opacity: 0.5;
}
.navigation__button-login {
  width: 140px;
  padding-top: 3px;
  padding-bottom: 3px;
  margin-left: 60px;
  line-height: 36px;
  color: white;
}
@media (max-width: 1475px) {
  .navigation__item {
    margin-right: 25px;
    margin-left: 25px;
  }
  .navigation__button-login {
    margin-left: 30px;
  }
}
@media (max-width: 1024px) {
  .navigation__button-login {
    align-self: center;
  }
  .navigation__item {
    margin-right: 15px;
    margin-left: 15px;
  }
}
@media (max-width: 1024px) and (max-width: 768px) {
  .navigation {
    flex-direction: column;
  }
  .navigation__list {
    flex-direction: column;
    margin-top: 15px;
  }
  .navigation__item {
    width: 100%;
    margin: 0;
    margin-bottom: 15px;
    text-align: center;
  }
  .navigation__link {
    font-size: 20px;
    padding: 10px 30px;
  }
  .navigation__button-login {
    width: 200px;
    margin: 0;
  }
}
